

import React,{Component} from "react";
import "./index.scss";

import {Head} from "../../components/head";
import { List, InputItem, WhiteSpace,WingBlank,Button } from 'antd-mobile';

export class Login extends Component{
    render(){
        return (
            <div>
                <Head title="" show={true} history={this.props.history} />
                <List>
                    <WingBlank>
                    <InputItem
                        
                        clear
                        placeholder=""
                        ref={el => this.autoFocusInst = el}
                    ></InputItem>
                    <WhiteSpace size="lg"/>
                    <InputItem
                    
                        clear
                        placeholder=""
                        ref={el => this.inputRef = el}
                    >
                    <Button type="warning" className="code" size="small"></Button>
                    </InputItem>
                     <WhiteSpace size="lg"/>
                    <Button type="primary" size="large"></Button>
                    </WingBlank>
                </List>
            </div>
        )
    }
}


